import React from 'react';
import spinner34 from "./spinner34.gif";

export const LoadingSpinner = () => {
    return (
        <div className='container' >
            <p id='mining'>Mining...</p>
            <img
                src={spinner34}
                alt='Loading...'
                style={{ width: '200px', margin: 'auto' }}
            />
        </div>
    );
};
