import './App.css';
import React, { Component } from 'react';
import Navbar from './Components/Layout/Navbar';
import Users from './Components/users/Users';
import axios from 'axios';
import BrandName from './Components/Layout/BrandName';
import Search from './Components/users/Search';
import { Alert } from './Components/Layout/Alert';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Profile from './Components/Pages/About';
import User from './Components/users/User';

class App extends Component {
    state = {
        users: [],
        user:{},
        loading: false,
        alert: null,
    };

    // async componentDidMount() {
    //     console.log(process.env.REACT_APP_GITHUB_CLIENT_SECRET);
    //     this.setState({ loading: true });
    //     const res = await axios.get(`https://api.github.com/users?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client?secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`);
    //     this.setState({ users: res.data, loading: false });
    // }

    //search github users
    searchUsers = async (text) => {
        this.setState({ loading: true });
        const res = await axios.get(
            `https://api.github.com/search/users?q=${text}&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client?secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`
        );
        this.setState({ users: res.data.items, loading: false });
    };

    //get a single user 
    getUser = async (username)=>{
        this.setState({ loading: true });
        const res = await axios.get(
            `https://api.github.com/users/${username}&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client?secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`
        );
        this.setState({ user: res.data, loading: false });
    };


    //clear users from window
    clearUsers = () => {
        this.setState({ users: [], loading: false });
    };

    setAlert = (msg, type) => {
        this.setState({ alert: { msg: msg, type: type } });
        setTimeout(() => this.setState({ alert: null }), 2000);
    };

    render() {
        return (
            <Router>
                <div className='App'>
                    <Switch>
                        <Route exact path='/'>
                            <div className='brand-and-nav'>
                                <Navbar
                                    home='Home'
                                    about='About Me'
                                    icon='fa fa-github'
                                />
                                <BrandName title={'GitHub Lense'} />
                                <div className='search-users'>
                                    <Alert alert={this.state.alert} />
                                    <Search
                                        searchUsers={this.searchUsers}
                                        clearUsers={this.clearUsers}
                                        showClear={
                                            this.state.users.length > 0
                                                ? true
                                                : false
                                        }
                                        showFinder={
                                            this.state.users.length === 0
                                                ? true
                                                : false
                                        }
                                        setAlert={this.setAlert}
                                    />
                                </div>
                            </div>
                            <div className='search-users'>
                                <div className='user-container'>
                                    <Users
                                        loading={this.state.loading}
                                        users={this.state.users}
                                    />
                                </div>
                            </div>
                        </Route>
                        <Route path='/About'>
                            <div className='brand-and-nav'>
                                <Navbar
                                    home='Home'
                                    about='About Me'
                                    icon='fa fa-github'
                                />
                                <BrandName title={'GitHub Lense'} />
                                <div>
                                    <Profile />
                                </div>
                            </div>
                        </Route>
                        <Route exact path='/user/:login' render={props => (
                            <User {...props}  getUser={this.getUser} user= {this.state.user} loading={this.state.loading}/>
                        ) }/>
                            
                    </Switch>
                </div>
            </Router>
        );
    }
}

export default App;
